* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'poppins', 'sans-serif';
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    background-color: rgb(19, 19, 204);
}

.container {
    width: 650px;
    background: #fff;
    border-radius: 15px;
    padding: 30px 30px 25px;

}

.container header {
    font-size: 35px;
    font-weight: 600;
    text-align: center;
    color: deeppink
}

.container .content {
    margin: 35px 0;
}

.content .quotes-area {
    display: flex;
    justify-content: center;
}

.quotes-area p {
    font-size: 22px;
    text-align: center;
}

.content .author-name {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
    font-size: 18px;
    font-style: italic;
}

.author-name span {
    margin: -7px 5px 0 0;
}

.container button {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 5px;
    padding: 8px 35px;
    font-size: 20px;
    background-color: #e44d4d6b;
    font-weight: 600;
    margin: 0 245px;
    cursor: pointer;
}